.block-about {
	&__container {
	}
	&__inner {
		display: flex;
		justify-content: space-between;
		gap: 40px;
		&>div {
			flex: 1 1 50%;
		}
	}
	&__content {
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		gap: 40px;
	}
	&__footer {
		display: flex;
		justify-content: space-between;
		padding: 30px 30px 50px 30px;
		border-top: #093359 2px solid;
		border-bottom: #093359 2px solid;
		gap: 30px;
		&-item {
			&-number {
				font-family: var(--third-family);
				font-weight: 500;
				font-size: 50px;
				color: $text-blue;
				span {
					font-size: 30px;
				}
			}
			&-text {
				margin-top: 20px;
			}
		}
	}
	&__img {
		width: 100%;
		height: 100%;
	}
	@media screen and (max-width: $md) {
		&__container {
			padding-top: 50px;
			flex-direction: column;
		}
		&__inner {
			flex-direction: column;
		}
		&__content {
			gap: 20px;
		}
		&__text {
		}
		&__btn {
			width: 209px;
			height: 37px;
		}
		&__img-wrapper {
			margin-top: 30px;
			width: 100%;
		}
		&__footer {
			// flex-direction: column;
			padding: 30px 0;
			gap: 0;
			display: grid;
			grid-template-columns: repeat(2, 1fr);
			&-item {
				&:nth-child(1),
				&:nth-child(2) {
					padding-top: 10px;
					padding-bottom: 30px;
					position: relative;
				}
				&:nth-child(1) {
					&::after {
						content: "";
						position: absolute;
						bottom: 0;
						left: -5px;
						display: block;
						border-bottom: 1px solid rgba(110, 117, 123, 0.3);
						width: calc(100% - 5px);
					}
				}
				&:nth-child(2) {
					&::after {
						content: "";
						position: absolute;
						bottom: 0;
						right: -5px;
						display: block;
						border-bottom: 1px solid rgba(110, 117, 123, 0.3);
						width: calc(100% - 5px);
					}
				}
				&:nth-child(1),
				&:nth-child(3) {
					padding-right: 30px;
					border-right: 1px solid rgba(110, 117, 123, 0.3);
				}
				&:nth-child(2),
				&:nth-child(4) {
					padding-left: 30px;
				}
				&:nth-child(3),
				&:nth-child(4) {
					padding-top: 30px;
					padding-bottom: 10px;
				}
				&-number {
					font-size: 37px;
					span {
						font-size: 22px;
					}
				}
				&-text {
					margin-top: 5px;
				}
			}
		}
	}
}
