.header {
	position: sticky;
	top: 0;
	z-index: 2;
	background-color: #fff;
	border-bottom: $base-color 2px solid;
	padding: 30px 0 20px 0;
	&__container {
		display: flex;
		justify-content: space-between;
	}

	&__inner {
		display: flex;
		flex-direction: column;
		gap: 30px;
	}
	&__contacts {
		display: flex;
		justify-content: right;
		align-items: center;
		gap: 20px;
	}
	&__icons {
		display: flex;
		align-items: center;
		gap: 20px;
	}
	&__icon {
		width: 25px;
		height: 25px;
	}
	&__btn {
		width: 200px;
		&--mobile {
			display: none;
			width: 30px;
			height: 30px;
		}
	}
	&__phone,
	&__phone a {
		font-weight: 500;
		font-size: 20px;
		line-height: 1.3;
		color: $base-color;
		text-decoration: none;
	}
	.menu {
		display: flex;
		gap: 70px;
		&__list {
			display: flex;
			justify-content: space-between;
			gap: 40px;
			list-style: none;
		}
		&__submenu {
			display: none;
			padding: 30px 40px 45px;
			position: absolute;
			top: 53px;
			box-shadow: 0 4px 4px 0 rgba(10, 67, 120, 0.25);
			border-bottom: $base-color 2px solid;
			background-color: $base-gray;
			max-height: 450px;
			z-index: 2;
			&-list {
				width: 100%;
				display: flex;
				list-style: none;
				flex-direction: column;
				&--about {
					text-wrap: nowrap;
					gap: 20px;
				}
			}
			&--active {
				display: flex;
			}
			&--uslugi {
				position: fixed;
				top: 160px;
				left: 50%;
				height: 450px;
				// width: calc(100% - paddings($container-padding-left, $container-padding-right));
				transform: translateX(-50%);
			}
		}
		&__item {
			position: relative;
			cursor: pointer;
			&--parent {
				margin-right: 20px;
				&::after {
					content: "";
					display: block;
					width: 12px;
					height: 8px;
					position: absolute;
					top: 55%;
					transform: translateY(-50%);
					right: -20px;
					background-image: url("../img/sprites/arrow-down.svg");
				}
			}
		}
		&__link {
			font-family: var(--font-family);
			font-size: 20px;
			line-height: 1.5;
			color: #4d565d;
		}
		.lang-switch {
			display: flex;
			align-items: center;
			gap: 25px;
			&__icon {
				height: 17px;
				width: 17px;
			}
			&__list {
				display: flex;
				align-items: center;
				gap: 6px;
			}
			&__link {
				color: #4d565d;
				font-weight: 500;
			}
			&__divider {
				width: 1px;
				height: 14px;
				background-color: black;
			}
		}
	}
	@media screen and (max-width: $lg) {
		padding: 20px;
	}
	@media screen and (max-width: $md) {
		&__container {
			gap: 44px;
		}
		&__contacts {
			flex-wrap: wrap;
			gap: 16px;
		}
		&__icons {
			gap: 12px;
		}
		&__icon {
			height: 18px;
			a {
				display: block;
				height: 100%;
				img {
					height: 100%;
				}
			}
		}
		&__phone {
			font-size: 15px;
		}
		&__btn {
			&--mobile {
				width: 50px;
				height: 50px;
				display: flex;
			}
		}
		.menu {
			display: none;
		}
	}
	@media screen and (max-width: $sm) {
		&__btn {
			width: 122px;
			height: 30px;
			&--mobile {
				width: 30px;
				height: 30px;
			}
		 }
		
	}
}

.categories-list {
	display: flex;
	flex-direction: column;
	flex-wrap: wrap;
	align-items: stretch;
	align-content: space-between;
	text-wrap: wrap;
	list-style: none;
	width: 100%;
	gap: 10px;
	&__item {
		width: 315px;
		font-family: var(--font-family);
		font-weight: 400;
		color: #0a4378;
		&--tree {
			position: relative;
			& > a::after {
				content: "+";
				position: absolute;
				right: -20px;
				font-weight: 500;
				font-size: 20px;
				color: #0a4378;
				line-height: 1;
				top: 50%;
				transform: translateY(-50%);
			}
		}
	}
	&__link {
		position: relative;
		color: #000;
		text-wrap: wrap;

		&:hover {
			color: #0a4378;
		}
	}
	&__sublist {
		list-style: none;
		display: flex;
		flex-direction: column;
		margin-top: 10px;
		gap: 10px;
		padding-left: 20px;
		border-left: 1px solid rgba(110, 117, 123, 0.3);
		.categories-list__item {
			width: 295px;
		}
		&--collapsed {
		}
	}
	&.categories-list--practice {
		gap: 30px;
		.categories-list__item--tree > a {
			font-weight: 500;
			&::after {
				content: "";
			}
		}
	}
}
