$promo-inner-padding-left: 173px;
.promo {
	padding: 0;
	max-width: initial;
	position: relative;

	&__container {
		position: relative;
	}
	&__bg-wrapper {
		top: 0;
		position: absolute;
		z-index: -1;
		width: 100%;
		height: 100%;
	}
	&__inner {
		max-width: 850px;
		padding-top: 160px;
		padding-bottom: 260px;
		padding-left: $promo-inner-padding-left;
		margin-left: -$promo-inner-padding-left;
		transform: translateY(1px);
		display: flex;
		flex-direction: column;
		height: 100%;
		background: #fff;
		border: 1px solid #000;
	}
	&__title {
		.name {
			font-family: var(--font4);
			font-weight: 500;
			font-size: 76px;
			color: #0c4377;
			display: block;
			margin-bottom: 20px;
		}
		.sub {
			font-family: var(--font-family);
			font-weight: 500;
			font-size: 30px;
			line-height: 1.12;
			color: #000;
			.underline {
				color: #0c4377;
				position: relative;
				&::after {
					content: "";
					position: absolute;
					bottom: -4px;
					left: 0;
					border: 1px solid #960d1b;
					width: 180px;
					height: 0px;
				}
			}
		}
	}
	&__subtitle {
		margin-top: 20px;
		font-family: var(--font3);
		font-weight: 400;
		font-size: 22px;
		line-height: 1.53;
	}
	&__btn {
		margin-top: 50px;
	}
	&__bg {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
	&__swiper {
		position: absolute;
		bottom: 0px;
		gap: 151px;
		padding: 20px;
		padding-left: 46px;
		display: flex;
		justify-content: space-between;
		background: #fff;
		&-text {
			position: relative;
			font-family: var(--font3);
			font-weight: 400;
			font-size: 20px;
			line-height: 1.68;
			&::before {
				content: "";
				box-sizing: border-box;
				position: absolute;
				left: -46px;
				top: 8px;
				display: block;
				width: 20px;
				height: 20px;
				border-radius: 100%;
				background-color: #960d1b;
				stroke-width: 3px;
				border: #a42e3a 3px solid;
			}
		}
		&-btns {
			display: flex;
			gap: 20px;
		}
		&-btn {
			cursor: pointer;
			height: 50px;
			display: flex;
			justify-content: center;
			align-items: center;
			width: 80px;
		}
	}
	@media screen and (max-width: $lg) {
		&__inner {
			padding-left: 193px;
		}
		&__swiper {
			padding-left: 66px;
		}
	}
	@media screen and (max-width: $md) {
		&__container {
			padding-top: 127px;
			padding-bottom: 100px;
		}
		&__inner {
			max-width: 100%;
			padding-top: 70px;
			padding-left: 20px;
			padding-bottom: 0;
			margin-left: initial;
			display: flex;
			flex-direction: column;
			transform: initial;
			background: #fff;
			border: none;
		}
		&__bg {
			object-position: top;
		}
		&__title {
			.name {
				font-size: 51px;
				margin-bottom: 10px;
			}
			.sub {
				font-size: 20px;
				line-height: 1.12;
				text-transform: none;
				.underline {
					&::after {
						border: 0.67px solid #960d1b;
						width: 121px;
					}
				}
			}
		}
		&__subtitle {
			font-size: 16px;
		}
		&__btn {
			margin-top: 30px;
		}
		&__swiper {
			position: relative;
			flex-direction: column;
			gap: 30px;
			padding-left: 55px;
			&-text {
				font-size: 16px;
				br {
					display: none;
				}
				&::before {
					width: 15px;
					height: 15px;
					left: -35px;
				}
			}
			&-btns {
				justify-content: flex-end;
			}
			&-btn {
				width: 60px;
				height: 38px;
				svg {
					height: 11px;
					width: 25px;
				}
			}
		}
	}
}
