h1,
h2,
h3,
h4,
h5,
h6,
ul {
	margin: 0;
	padding: 0;
}
* {
	box-sizing: border-box;
	&::-webkit-scrollbar {
		width: 5px;
		height: 110px;
	}
	&::-webkit-scrollbar-track {
		background: #fff;
	}
	&::-webkit-scrollbar-thumb {
		background-color: #0a4378;
		border-radius: 10px;
	}
}
:root {
	line-height: 1.65;
	font-family: var(--font-family);
	font-weight: 400;
	font-size: 16px;
	@media screen and (max-width: $sm) {
		font-size: 12px;
	}
}

h2 {
	font-weight: 400;
	font-size: 35px;
	@media screen and (max-width: $sm) {
		font-size: 26px;
	}
}
h3 {
	font-weight: 500;
	font-size: 16px;
}

a {
	text-decoration: none;
}
a.hashtag {
	color: #4d565d;
	font-weight: 400;
	&::before {
		content: "#";
		margin-right: 10px;
	}
}
section {
	margin: 0 auto;
	max-width: 1430px;
	padding-left: $container-padding-left + px;
	padding-right: $container-padding-right + px;
	@media screen and (max-width: $lg) {
		max-width: 100%;
		padding-left: 20px;
		padding-right: 20px;
	}
	@media screen and (max-width: $sm) {
	}
}

main section {
	& + section {
		margin-top: 70px;
		@media screen and (max-width: $md) {
			margin-top: 0;
		}
	}
}

.container {
	max-width: 1180px;
	width: 100%;
	margin: 0 auto;
	@media screen and (max-width: $lg) {
		max-width: 100%;
	}
	@media screen and (max-width: $md) {
		max-width: 100%;
	}
}

button {
	cursor: pointer;
}

.btn {
	width: 280px;
	height: 50px;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: $base-color;
	text-align: center;
	border: none;
	color: #fff;
}

.title {
	font-family: var(--font-family);
	color: #000;
	font-weight: 500;
	font-size: 16px;
	line-height: 1.31;
	text-transform: uppercase;
	&--vert {
		padding: 20px;

		text-align: center;
		color: #0a4378;
		border-left: 2px solid $text-blue;
		border-right: 2px solid $text-blue;
		writing-mode: vertical-rl;
		transform: rotate(-180deg);
	}
}
