.testimonials {
	&__container {
		display: flex;
		gap: 60px;
	}
	&__wrapper {
		display: flex;
		flex-direction: column;
	}
	&__testimonial {
		padding-top: 25px;
		padding-bottom: 25px;
		padding-right: 1px;
		font-style: italic;
		&-text {
		}
		&-author {
			font-weight: 500;
			margin-top: 25px;
			text-align: right;
		}
		&--left {
			padding-right: 75px;
			border-right: rgba(110, 117, 123, 0.3) 1px solid;
		}
		&--right {
			padding-right: 25px;
			padding-left: 60px;
		}
	}
	&__btns {
		margin-top: 40px;
		display: flex;
		justify-content: flex-end;
		gap: 20px;
	}
	&__btn {
		padding: 16px 23px;
		border: none;
		cursor: pointer;
		background: $base-gray;
	}
	@media screen and (max-width: $md) {
		&__container {
			gap: 35px;
			padding: 45px 0;
		}
		&__testimonial {
			&--left {
				padding-right: 10px;
				border-right: none;
			}
			&--right {
				padding-right: 0;
				padding-left: 0;
			}
		}
	}
}
