.geography {
	&__container {
		display: flex;
	}
	&__img-wrapper {
		max-width: 760px;
	}
	&__img {
		width: 100%;
		height: 100%;
	}
	&__text {
		width: 420px;
	}
	&__desc {
		margin-top: 30px;
	}
	@media screen and (max-width: $md) {
		&__container {
			padding: 43px 0;
			flex-direction: column;
		}
		&__img-wrapper {
			margin-top: 13px;
			order: 2;
			max-width: 100%;
		}
		&__text {
			width: 100%;
		}
	}
}
