.projects {
	position: relative;
	.block-tabs__tab-content {
		flex-direction: column;
	}
	&__grid {
		&-header {
			text-transform: uppercase;
		}
		&-hashtag {
			color: #4d565d;

			&::before {
				content: "#";
				margin-right: 10px;
			}
		}
		&-row {
			display: grid;
			grid-template-columns: 0.6fr 0.85fr 1fr;
			padding: 20px;
			column-gap: 90px;
		}
		&-project {
			border-top: 1px solid rgba(110, 117, 123, 0.3);
			&-name {
				display: flex;
				justify-content: space-between;
				flex-direction: column;
				font-weight: 500;
				h4 {
					font-weight: 500;
				}
			}
			&-text {
				ul {
					padding-left: 20px;
				}
			}
			&-title {
				display: none;
			}
			&:last-child {
				border-bottom: 1px solid rgba(110, 117, 123, 0.3);
			}
			&-footer {
				display: none;
			}
		}
	}
	&__btns {
		display: none;
	}
	@media screen and (max-width: $sm) {
		&__grid {
			&-header {
				display: none;
			}
			&-project {
				border-top: none;
				:last-child {
					border-bottom: none;
				}
				> div {
					padding-top: 18px;
					padding-bottom: 24px;
				}
				&-text {
					border-top: 1px solid rgba(110, 117, 123, 0.3);
				}
				&-name {
					gap: 0;
				}
				&:last-child {
					border-bottom: none;
				}
				&--mobile {
					.projects__grid-project-title {
						display: block;
						font-weight: 400;
						font-size: 16px;
						margin-bottom: 10px;
					}
				}
				&-footer {
					height: 80px;
					width: calc(100% - 185px);
					// margin-top: 50px;
					padding: 16px 20px;
					display: flex;
					justify-content: center;
					align-items: center;
				}
				&-link {
					font-size: 12px;
					text-decoration: underline;
					color: #0c4377;
				}
			}
			&-row {
				grid-template-columns: 1fr;
				grid-template-rows: 0.6fr 0.85fr 1fr;
			}
			&-hashtag {
				margin-top: 20px;
			}
		}
		&__btns {
			position: absolute;
			bottom: 0;
			right: 0;
			z-index: 2;
			height: 80px;
			width: 185px;
			padding: 16px 25px 0 25px;
			display: flex;
			justify-content: space-between;
			align-items: flex-start;
			background: #fff;
		}
		&__btn {
			width: 60px;
			height: 38px;
		}
		.block-tabs__container {
			padding-bottom: 0;
		}
	}
}
