.block-tabs {
	background-color: $base-gray;

	&__container {
		padding-top: 70px;
		padding-bottom: 93px;
	}
	&__header {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
	&__title {
	}
	&__tabs {
		display: flex;
		justify-content: space-between;
	}
	&__tab {
		cursor: pointer;
		width: 390px;
		height: 60px;
		display: inline-flex;
		align-items: center;
		justify-content: center;
		font-weight: 500;
		line-height: 1.3;
		text-transform: uppercase;
		color: $text-gray;
		border-bottom: 2px solid $text-blue;
		border-top: 2px solid $text-blue;
		&--active {
			color: $text-blue;
		}
	}
	&__content {
		margin-top: 60px;
		height: 100%;
	}
	&__tab-content {
		height: 100%;
		display: none;

		&--active {
			display: flex;
		}
	}
	&__col {
		flex: 1 1 calc(100% / 3);
	}
	&__footer {
		margin-top: 90px;
		display: flex;
		justify-content: space-between;
		align-items: center;
		gap: 70px;
		&-text {
			flex: 1;
		}
		&-btn {
			width: 235px;
		}
	}
	&__btns {
		display: none;
	}
	@media screen and (max-width: $md) {
		&__container {
			padding-top: 26px;
			padding-bottom: 0;
		}
		&__header {
			flex-direction: column;
			gap: 20px;
		}
		&__col {
			flex: none;
		}
		&__title {
			width: 100%;
			text-align: left;
		}
		&__tabs {
			width: 100%;
		}
		&__tab {
			height: 44px;
			border-bottom: 1.5px solid #1f4569;
			border-top: 1.5px solid #1f4569;
			flex: 1;
		}
		&__content {
			margin-top: 30px;
		}
		&__tab-content {
			overflow: hidden;
			gap: 20px;
		}
		&__footer {
			padding-top: 16px;
			padding-bottom: 26px;

		}
		&__footer-text {
			display: none;
		}
		&__btns {
			padding: 16px 25px;
			padding-bottom: 26px;

			display: flex;
			justify-content: space-between;
			align-items: flex-start;
			background-color: #fff;
			gap: 15px;
			position: absolute;
			right: 0;
			bottom: 0;
		}
		&__btn {
			width: 90px;
			height: 57px;
		}
	}
	@media screen and (max-width: $sm) {
		&__container {
		}
		&__col {
			flex: none;
		}
		&__footer {
			margin-top: 30px;
			gap: 20px;
		}
		&__footer-btn {
			width: 164px;
			height: 38px;
		}
		&__btn {
			width: 60px;
			height: 38px;
		}
	}
}

