.posts-swiper {
	display: flex;
	flex-direction: column;
	row-gap: 50px;
	position: initial;
	&__footer {
		display: flex;
		justify-content: space-between;
		padding-bottom: 57px;
	}
	&__tags {
		display: flex;
		justify-content: flex-start;
		align-content: flex-start;
		max-width: calc(100% - 290px);
		flex-wrap: wrap;
		column-gap: 40px;
		row-gap: 20px;
		span {
			font-weight: 500;
			color: #4d565d;
		}
	}
	&__tag {
		text-decoration: underline;
	}
	&__btns {
		position: absolute;
		right: 0;
		bottom: 0;
		padding: 40px 30px;
		padding-right: 120px;
		display: flex;
		gap: 20px;
		background: #fff;
	}
	&__btn {
		padding: 0;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 80px;
		height: 50px;
	}
	@media screen and (max-width: $md) {
		&__btns {
			padding: 27px 32px;
			padding-left: 18px;
			position: relative;
			right: initial;
			gap: 15px;
			right: -12px;
		}
		
		&__btn {
			width: 60px;
			height: 38px;
		}
	}
}

