.footer {
	margin-top: 90px;
	padding: 50px 0 20px;
	border-top: $base-color 2px solid;

	&__logo {
		span {
			display: block;
			color: #4d565d;
			margin-top: 30px;
		}
	}
	&__inner {
		display: flex;
		justify-content: space-between;
		&--mobile {
			display: none;
		}
	}
	&__block {
		flex: 1;
		&-title {
			text-transform: uppercase;
			color: #4d565d;
			margin-bottom: 12px;
		}
	}
	&__menu {
		.categories-list {
			gap: 10px;
			width: initial;
			&__item {
				width: initial;
				&--tree > a::after {
					display: none;
				}
			}
		}

		&-title {
			text-transform: uppercase;
			margin-bottom: 12px;
		}
		&-inner {
			display: flex;
			justify-content: space-between;
		}
		&--about {
			margin-top: 50px;
		}
	}
	&__contacts {
		margin-top: 40px;
		display: flex;
		flex-direction: column;
		gap: 20px;
		a {
			color: #000;
		}
	}
	&__contact {
		display: flex;
		align-items: center;
		gap: 30px;
	}
	&__btn {
		margin-top: 35px;
		width: 200px;
	}
	&__copyright {
		margin-top: 80px;
		display: flex;
		justify-content: space-between;
		.info {
			text-decoration: underline;
			text-decoration-skip-ink: none;
			color: #0c4377;
		}
		.author {
			color: #000;
		}
	}
	@media screen and (max-width: $lg) {
		&__container {
			padding: 0 20px;
		}
	}
	@media screen and (max-width: $sm) {
		padding: 35px 0 20px;
		&__logo {
			display: flex;
			flex-direction: row;
			align-items: center;
			justify-content: space-between;
		}
		&__inner {
			display: none;
		}
		&__inner--mobile {
			display: flex;
			flex-direction: column;
			background-color: #fff;
		}
		&__tabs {
			margin-top: 30px;
		}
		&__menu {
			display: flex;
			justify-content: space-between;
			width: 100%;
			gap: 10px;
			&-inner {
				// flex-direction: column;
			}
		}
		&__contacts {
			gap: 12px;
			&-inner {
				display: flex;
				align-items: center;
				gap: 16px;
			}
		}
		&__contact {
			gap: 22px;
		}
		&__icons {
			display: flex;
			gap: 10px;
		}
		&__icon {
			height: 18px;
			a {
				display: block;
				height: 100%;
				img {
					height: 100%;
				}
			}
		}
		&__btn {
			margin-top: 0;
			margin-left: 14px;
			width: 139px;
			height: 37px;
		}
		&__copyright {
			margin-top: 30px;
			flex-direction: column;
			gap: 20px;
		}
	}
}
